import { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useForm } from 'react-hook-form';
import { toast } from "react-toastify";

import { addCaregiverScaleQuestions, getCaregiverScaleQuestions } from '../../../store/actions/question.action.js';

import { useFormContext } from '../../../context/FormContextProvider.js';
import { PageCard } from "../../commonComponents/Cards/HelperCards.jsx";
import { useTitle } from '../../../helpers/custom-hooks.js';
import { processScaleOptions } from '../../../helpers/form.helper.js';

const CaregiverSectionWrapper = () => {
	const navigate = useNavigate();
	const { checkFormProgress_c, getFormProgress_c } = useFormContext();
	const FORM_PROGRESS = getFormProgress_c();
	const [isNavigationNeeded, navigateTo] = checkFormProgress_c(FORM_PROGRESS.caregiverScale);


	useEffect(() => {
		// check the progress of the form being filled
		if (isNavigationNeeded) {
			navigate(navigateTo);
		}
	}, []);

	return (
		<>
			{isNavigationNeeded ? null : <CaregiverSection />}
		</>
	);
};

const CaregiverSection = () => {
	const [isLoading, setIsLoading] = useState(false);
	const [alert, setAlert] = useState(true);
	const [sections, setSections] = useState([]);
	const { register, handleSubmit, formState: { errors } } = useForm();
	const {
		getNavigateTo_c,
		getFormProgress_c,
		getPatientId_c,
		setFormProgress_c,
	} = useFormContext();

	const navigate = useNavigate();
	const patient_id = getPatientId_c();
	const NAVIGATE_TO = getNavigateTo_c();
	const FORM_PROGRESS = getFormProgress_c();



	useTitle('Caregiver Scale');
	useEffect(() => {
		_getCaregiverScaleQuestions();
	}, []);


	async function _getCaregiverScaleQuestions() {
		setIsLoading(true);
		const questions = await getCaregiverScaleQuestions();
		if (questions.status === 200) {
			setSections(questions.data);
			setIsLoading(false);
		} else if (questions.status >= 400) {
			toast.error(questions.message.message);
		}
	}

	async function handleData(data) {

		const finalObject = processScaleOptions({ patient_id, data });
		const response = await addCaregiverScaleQuestions(finalObject);

		if (response.status === 200) {
			setFormProgress_c(FORM_PROGRESS.childScale);
			navigate(NAVIGATE_TO.childScale);
		} else if (response.status >= 400) {
			toast.error(response.message.message);
		}
	}


	{/* remove them when done with testing */ }
	// function _setCookieAndLocalStorage() {
	// 	setCookieAndLocalStorage_c();
	// 	navigate(NAVIGATE_TO.caregiverScale);
	// }
	// function _resetCookieAndLocalStorage() {
	// 	resetCookieAndLocalStorage();
	// 	navigate(NAVIGATE_TO.patient);
	// }

	return (
		<PageCard title={`Caregiver Scale`} isLoading={isLoading} paddingSmall={true} >

			{alert ? <Alert variant="info" dismissible onClose={() => setAlert(false)} >
				<Alert.Heading>Caregiver Scale</Alert.Heading>
				<hr />
				<p className="mb-0">
					This is Caregiver's Scale form. Please only fill Caregiver's details here.
				</p>
			</Alert> : null}

			{/* <Button onClick={() => _setCookieAndLocalStorage()} variant="outline-primary" className='my-3 border border-primary'>Set Cookie and Local Storage - UpTo Child</Button>
			<Button onClick={() => _resetCookieAndLocalStorage()} variant="outline-danger" className='my-3 border border-danger'>Reset Cookie and Local Storage</Button> */}


			<div style={isLoading ? { pointerEvents: `none`, opacity: 0.5 } : {}}>
				<Form onSubmit={handleSubmit(handleData)} className='mt-10'>

					{sections.map((section, index) => (
						<>
							<Card className="mb-3 border border-3 p-3" key={index}>
								<div className='d-flex justify-content-center'>
									<Card.Title>{section.heading}</Card.Title>
								</div>
								{/* Section Discription */}
								<div className="mb-2 text-muted">
									{section.description?.split(',')?.map((questionDescription, index) => (
										<>
											<div key={index} className='px-auto'>
												{section.is_description_in_urdu ?
													<div className='d-flex justify-content-end'>
														<p key={index} className=''>{questionDescription}</p>
													</div>
													:
													<p key={index} className=''>{questionDescription}</p>
												}
											</div>
										</>
									))}
								</div>



								<Card.Body className='p-0'>

									{/* Section Options */}

									<Row className='text-bold'>
										<Col className='col-1'>
											No.
										</Col>
										<Col className='col-4'>
											Questions
										</Col>
										<Col className='m-0 p-0'>
											<Row className='m-0 p-0'>
												{section.options.map((option, i) => (
													<Col key={i} className='small p-0 m-0'>
														{option.option_text}
													</Col>
												))}
											</Row>
										</Col>
									</Row>


									{/* Section Questions */}
									{section.section_questions.map((question, index) => (
										<>
											<Row className='mt-5' key={index}>
												<Col className='col-1'>
													{index + 1}
												</Col>
												<Col className='col-4'>
													{question.is_question_in_urdu ? <div className='d-flex justify-content-end'>{question.question_text}</div> : question.question_text}
												</Col>
												<Col className='m-0 p-0'>
													<Row className='m-0 p-0'>
														{section.options.map((option, i) => (
															<Col className='small p-0 m-0' key={i}>
																<Form.Check type="radio" {...register(`${patient_id}-${section.id}-${question.id}`, { required: !question.is_nullable })} value={`${option.id}-${Number(option.is_option_in_urdu)}-${option.option_text}`} />
															</Col>
														))}
													</Row>
												</Col>
											</Row>
											{errors[`${patient_id}-${section.id}-${question.id}`] && <span className='text-danger ps-20'>This field is required</span>}
										</>
									))}
								</Card.Body>
							</Card>
						</>
					))}

					<Form.Group className="mb-3">
						<Button className="mb-3 float-end" variant="primary" type="submit">
							Next
						</Button>
					</Form.Group>

				</Form>
			</div>


		</PageCard>
	);
};

export default CaregiverSectionWrapper;